import i18n from 'i18next';
import {initReactI18next} from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    resources: {
      ko: {
        translation: {
          welcome: '',
          login: {
            title: '',
            username: ' ',
            password: '',
            submit: '',
          },
        },
      },
      en: {
        translation: {
          welcome: 'Welcome',
          login: {
            title: 'Login',
            username: 'Username',
            password: 'Password',
            submit: 'Sign In',
          },
        },
      },
    },
    fallbackLng: 'ko',
    interpolation: {
      escapeValue: false,
    },
  });

export default i18n;
